.adder__form_div{

    width: 80%;
    height: 50%;
    border-radius: 10px;
    padding: 3rem;
    background: white;
    margin: 4% auto;

}
#add__task__input{
    border-radius: 5px;
    border: 2px solid #818181;
    width: 35%;
    height: 45px;
}
#add__task__input:active,#add__task__input:focus{
    background:  #818181;
    color: white;
    font-size: 1.4rem;
    cursor: default;
}
#add__task__btn{
    background: white;
    padding: 10px;
    border-radius: 5px;
    font-size: 1.5rem;
    font-weight: bold;
}
#add__task__btn:active{
    background: #61dafb;
}
.formControl{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
}