.column__outer_div{

    height: 90vh;
    width: 20%;

    border-radius: 15px;
    border: 7px solid black;

}

.column__body{
    height: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}

.column__title{

    background: #61dafb;
    padding: 0.15rem;
    position: relative;
    top: 0;
   border-radius: 5px;

}
@media only screen and (max-width: 767px) {
    .column__outer_div{

        height: auto;
        width: 90%;

        border-radius: 15px;
        border: 7px solid black;

    }
    .column__body{
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-around;
        flex-wrap: wrap;
        padding: 3px;
    }

}
